import React from 'react';
import Gallery from 'react-photo-gallery';
import { Link } from 'react-router-dom';

import PageWrapper from '../shared/PageWrapper';
import { categories } from '../shared/photos';


export default function Categories() {
	return(
		<PageWrapper>
			<div className="main-content">
				<Gallery photos={categories} direction="column" margin={12} renderImage={(item, i) => {
					const { photo } = item;
					
					return (
						<Link className="category" key={item.index} to={'/album/' + photo.slug} width={photo.width} height={photo.height}>
							<div className="category-overlay" style={{backgroundColor: photo.overlayColor}}></div>
							<img className="category-img" src={photo.src} width={photo.width} height={photo.height}/>
							<h2 className="category-title">
								{photo.title}
							</h2>
						</Link>
					);
				}}/>
			</div>
		</PageWrapper>
	);
}