import React, { useState, useCallback } from 'react';
import Gallery from 'react-photo-gallery';
import Carousel, { Modal, ModalGateway } from 'react-images';

import PageWrapper from '../shared/PageWrapper';
import TextHeader from '../shared/TextHeader';
import Footer from '../shared/Footer';

const images = importAll(require.context('../../assets/images/', true, /\.(gif|jpg|jpeg|tiff|png)$/));

export default function Album({ match }) {
	const [currentImage, setCurrentImage] = useState(0);
	const [viewerIsOpen, setViewerIsOpen] = useState(false);

	const { albumName } = match.params;
	
	const openLightbox = useCallback((event, { photo, index }) => {
		setCurrentImage(index);
		setViewerIsOpen(true);
	}, []);

	const closeLightbox = () => {
		setCurrentImage(0);
		setViewerIsOpen(false);
	};

	const generateImages = (category)  => {
		return images.reduce((res, pathName) => {
			if (pathName.includes(category)) {
				const ratio = pathName.split('-')[2];
				const sizes = ratio.split('.')[0].split('x');

				res.push({
					src: pathName,
					width: Number(sizes[0]),
					height: Number(sizes[1]),
				});
			}
			return res;
		}, []);
	};

	const photos = generateImages(albumName);
	console.log(photos)
	
	return(
		<PageWrapper>
			<div className="main-content">
				{
					photos.length > 0 ?
						<div>
							<TextHeader
								alignment="center"
								title={albumName.split('_').join(' ').toUpperCase()}
							/>
							<Gallery photos={photos} direction="column" margin={12} onClick={openLightbox}>
								<ModalGateway>
									{
										viewerIsOpen ? 
											<Modal onClose={closeLightbox}>
												<Carousel
													currentIndex={currentImage}
													views={photos.map(x => ({
														...x,
														src: x.src,
														caption: x.title
													}))}
												/>
											</Modal>
											: 
											null
									}
								</ModalGateway>
							</Gallery>
						</div>
						:
						<div className="horizontal-centered">
							<TextHeader
								title="Empty album"
								subtitle="This album does not contain any images yet"
							/>
						</div>
				}
			</div>

			<Footer/>
		</PageWrapper>
	);
}


/**
 *  Uses webpack to import all files in a folder
 * @param {function} r webpackContext function
 */
function importAll(r) {
	return r.keys().map(r);
}